import React from 'react'
import axios from 'axios'

import './App.css'
import SocialFollow from "./SocialFollow"

class App extends React.Component {
    state = { advice: '' }

    componentDidMount() {        
        this.fetchAdvice()
    }

    fetchAdvice = () => {
        axios.get('https://api.adviceslip.com/advice')
        .then((response) => {
            const {advice} = response.data.slip ;
            this.setState({ advice: advice});
        })
        .catch((error) => {
            console.log(error); 
        });
    }

    render() {
        const { advice } = this.state ;
        return (
            <div>
            <div className="app">
            <h1>stephanie zendejo</h1>
            <span>hey there! i like to code.</span>
                     
          </div>
          <footer>
            <SocialFollow /> 
          </footer>
          </div>

        );
    }
}

export default App